@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

a{
  color: black;
  text-decoration: none;
}

ul{
  list-style: none;
}

.index{
  position: absolute;
}
button{
  cursor: pointer;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navbar{display:flex;justify-content:space-between;align-items:center;background-color:black;color:white;height:20vh;padding-inline:25px}.navbar .navbar__logo{height:80px;color:white}.navbar .navbar__menu{display:flex;list-style:none;justify-content:space-evenly;align-items:center}.navbar .navbar__menu .navLink{display:flex;flex-direction:column;align-items:center}.navbar .navbar__menu .navLink::after{-webkit-transform:translateY(-5px);transform:translateY(-5px);content:"";display:block;width:0%;border-bottom:solid 2px white;opacity:0}.navbar .navbar__menu .navLink:hover::after{opacity:1;width:90%;transition:all 500ms}.navbar .navbar__menu .navLinkActive::after{opacity:1;width:90%}

.navbar__item{border:none;background-color:transparent;color:white;font-size:1.5rem;padding:5px 10px}

.navbar__cartIcon{background-color:transparent;color:white;border:none;font-size:1.5rem;margin-inline:10px}.navbar__cartIcon:hover{color:#71ac71;transition:all 300ms}

.item{display:flex;flex-direction:column;border-radius:10px;box-shadow:0px 1px 5px 2px rgba(0,0,0,0.3)}.item:hover{-webkit-transform:scale(1.02);transform:scale(1.02);box-shadow:0px 5px 10px 2px rgba(0,0,0,0.3);transition:all 350ms}.item img{border-radius:10px 10px 0 0;width:100%;height:auto}.item .productInfo{display:flex;height:150px;flex-direction:column;justify-content:space-evenly;align-items:center;padding-inline:25px;border:1px solid #75808f;border-radius:0 0 10px 10px;border-top:none}.item .productInfo .details{width:100%;display:flex;align-items:flex-end;justify-content:space-between}.item .productInfo .details .description{display:inline-block;font-size:1.2rem;color:#40485f}.item .productInfo .details .price{color:#50ad50;font-weight:bold;font-size:1.2rem}.item .productInfo .title{font-weight:bold;font-size:1.5rem;color:#40485f}

.spinnerContainer{display:flex;justify-content:center;align-items:center;position:absolute;top:55%;left:48.55%}.spinnerContainer .spinnerIcon{-webkit-animation-name:rotation;animation-name:rotation;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite;-webkit-animation-timing-function:linear;animation-timing-function:linear;font-size:3.125rem;color:#2f2f2f;display:flex;height:50px;width:50px;color:#65707c}@-webkit-keyframes rotation{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes rotation{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

.itemList{display:grid;justify-content:space-evenly;align-items:center;width:80%;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:25px;grid-row-gap:25px;margin-top:50px}

.itemListContainer{padding:50px;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;text-align:center}

.itemCount{display:flex;flex-direction:column;align-items:center;justify-content:space-between;height:150px;width:250px;height:100px}.itemCount__buttons{display:flex}.itemCount__buttons button{border:none;color:#fff;border-radius:50px;font-size:1.25rem;font-weight:bolder;width:30px;height:30px;background-color:#75808f}.itemCount__buttons button:hover:enabled{background-color:white;border:1px solid #b2d4fa;-webkit-transform:scale(1.05);transform:scale(1.05);transition:ease-in-out all 200ms;color:#75808f;box-shadow:0px 0px 5px 0px #000}.itemCount__buttons button:disabled{background-color:#c5c9ce;cursor:default}.itemCount__buttons p{display:flex;align-items:center;justify-content:center;width:50px}.itemCount .stock{color:#a3a3a3}.itemCount .addToCart-btn{border:none;border-radius:5px;background-image:linear-gradient(to top, #4f6188, #6c7fa7);color:#fff;padding:10px;font-weight:bold;box-shadow:2px 3px 8px 0px rgba(0,0,0,0.8)}.itemCount .addToCart-btn:hover{-webkit-transform:scale(1.02);transform:scale(1.02);transition:all 250ms}

.itemDetail{box-sizing:border-box;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;width:80%;height:100%;border-radius:10px;box-shadow:0px 0px 8px 0px rgba(0,0,0,0.288);background-image:linear-gradient(#fafafa, #ebebeb);padding:50px}.itemDetail .title{color:#40485f}.itemDetail main{box-sizing:border-box;display:flex;align-items:top;width:100%;justify-content:space-evenly;padding:25px}.itemDetail main img{padding:5px;width:45%;height:auto;border-radius:10px;box-shadow:0px 0px 8px 0px rgba(0,0,0,0.5)}.itemDetail main .details{display:flex;flex-direction:column;justify-content:space-between;width:40%;border-left:1px solid #b4b4b4;padding-inline:25px}.itemDetail main .details .description{font-size:1.2rem;color:#40485f}.itemDetail main .details .price{color:#50ad50;font-weight:bold;font-size:2rem;align-self:flex-end}.itemDetail footer{width:100%;display:flex;flex-direction:column;align-items:center}.itemDetail footer .itemCount{width:90%;border-block:1px solid #b4b4b4;padding-block:25px}

.itemDetailContainer{box-sizing:border-box;display:flex;flex-direction:column;align-items:center;justify-content:center;height:80vh;padding:50px}

